<template>
  <div class="photo-wall" :style="{'background-image':'url('+imgUrls[selectedIndex]+')'}">
    <div class="title" v-if="coverLetter.length > 0">{{coverLetter}}</div>
    <div
      class="photo-preview"
      v-for="(url,i) in imgUrls"
      v-if="!autoDisplay"
      v-show="i!=selectedIndex"
      :style="{'background-image':'url('+url+')'}"
      @click="selectImage(i)"
    />
  </div>
</template>

<script>
import { clearInterval } from "timers";
export default {
  props: {
    imgUrls: {
      type: Array,
      required: false,
      default() {
        return [
          "https://images.unsplash.com/photo-1551776235-dde6d482980b?ixlib=rb-1.2.1&ixid=eyJhcHBfaWQiOjEyMDd9&auto=format&fit=crop&w=2134&q=80"
        ];
      }
    },
    autoDisplay: {
      type: Boolean,
      required: false,
      default: false
    },
    coverLetter: {
      type: String,
      required: false,
      default: ""
    }
  },
  data() {
    return {
      selectedIndex: 0,
      setIntervalID: undefined
    };
  },
  mounted() {
    this.setIntervalID = setInterval(() => {
      this.carousel();
    }, 3000);
  },
  destroyed() {
    if (this.setIntervalID) {
      window.clearInterval(this.setIntervalID);
    }
  },
  methods: {
    selectImage(i) {
      this.selectedIndex = i;
    },
    carousel() {
      if (this.autoDisplay) {
        this.selectedIndex = (this.selectedIndex + 1) % this.imgUrls.length;
      }
    }
  }
};
</script>

