import Vue from "vue";
import Router from "vue-router";
import Home from "./views/Home.vue";

Vue.use(Router);

export default new Router({
  scrollBehavior() {
    return {
      x: 0,
      y: 0
    }
  },
  routes: [{
      path: "/",
      name: "home",
      component: Home
    },
    {
      path: "/about",
      name: "about",
      // route level code-splitting
      // this generates a separate chunk (about.[hash].js) for this route
      // which is lazy-loaded when the route is visited.
      component: function () {
        return import( /* webpackChunkName: "about" */ "./views/About.vue");
      }
    },
    {
      path: "/rooms",
      name: "rooms",
      component: function () {
        return import( /* webpackChunkName: "about" */ "./views/Rooms.vue");
      }
    },
    {
      path: "/rooms/:id",
      name: "room",
      component: function () {
        return import( /* webpackChunkName: "about" */ "./views/Room.vue");
      }
    },
    {
      path: "/reservationResult",
      name: "reservationResult",
      component: function () {
        return import( /* webpackChunkName: "about" */ "./views/ReservationResult.vue");
      }
    },
  ]
});